* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  background-color: #ff3cac;
  background-image: linear-gradient(
    225deg,
    #ff3cac 0%,
    #784ba0 50%,
    #2b86c5 100%
  );
  color: white;
  font-family: "Do Hyeon", sans-serif;
  min-height: 100vh;
}
header {
  font-size: 2rem;
}

header,
form {
  min-height: 16vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

form input,
form button {
  padding: 0.5rem;
  font-size: 1.8rem;
  border: none;
  background: white;
  box-shadow: 3px 3px 3px 2px rgba(0, 0, 0, 0.2);
  font-family: "Do Hyeon", sans-serif;
}
form input {
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
  outline: none;
}
form input::placeholder {
  color: rgba(186, 81, 235, 0.658);
  font-size: 1.2rem;
}
form button {
  color: #807e7e;
  background: #f6fffe;
  cursor: pointer;
  transition: all 0.3s ease;
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
}
form button:hover {
  background: #5f5e5e;
  color: rgb(255, 255, 255);
}
.todo-container {
  display: flex;
  justify-content: center;
  background-image: url(./img/fondo2.png);
  background-size: cover;
  background-position: top;
  padding: 60px 0;
  width: 35%;
  min-height: 50vh;
  margin: 0 auto 50px auto;
}

.todo-list {
  width: 70%;
  list-style: none;
}

.todo {
  margin: 0.3rem;
  font-size: 1rem;
  color: black;
  display: flex;
  align-items: center;
  transition: all 1s ease;
}
.filter-todo {
  padding: 1rem;
  border-radius: 10px;
}
.todo li {
  flex: 1;
}

.trash-btn,
.complete-btn {
  background: #4b4a4a;
  color: white;
  border: none;
  padding: 0.5rem 0.6rem;
  margin: 2px;
  cursor: pointer;
  font-size: 1rem;
  border-radius: 50px;
}
.complete-btn {
  background: rgb(11, 212, 128);
  margin-left: 20px;
}
.complete-btn:hover {
  background-color: rgb(0, 180, 105);
}
.btn-change {
  background-color: rgb(196, 59, 214);
}
.btn-change:hover {
  background-color: rgb(168, 40, 185);
}
.trash-btn:hover {
  background-color: rgb(126, 117, 117);
}
.todo-item {
  padding: 0rem 0.5rem;
}

.fa-trash,
.fa-check {
  pointer-events: none;
}

.fall {
  transform: translateY(10rem) rotateZ(20deg);
  opacity: 0;
}

.completed {
  text-decoration: line-through;
  opacity: 0.5;
}

/*CUSTOM SELECTOR */
select {
  -webkit-appearance: none;
  -moz-appearance: none;
  -ms-appearance: none;
  appearance: none;
  outline: 0;
  box-shadow: none;
  border: 0 !important;
  background-image: none;
}

/* Custom Select */
.select {
  margin: 1rem;
  position: relative;
  overflow: hidden;
  border-radius: 10px;
  box-shadow: 3px 3px 3px 2px rgba(0, 0, 0, 0.2);
}
select {
  font-family: "Do Hyeon", sans-serif;
  font-size: 19px;
  cursor: pointer;
  width: 12rem;
}
/* Arrow */
.select::after {
  content: "\25BC";
  position: absolute;
  top: 0;
  right: 0;
  padding: 1rem;
  background: #f6fffe;
  color: #b6b4b4;
  cursor: pointer;
  pointer-events: none;
}

.select:hover::after {
  background: #5f5e5e;
  color: white;
}

@media (max-width: 780px) {
  header {
    font-size: 1rem;
  }
  .todo-container {
    width: 65%;
  }
  .complete-btn,
  .trash-btn {
    padding: 0.3rem 0.4rem;
    margin: 1px;
    font-size: 0.8rem;
  }
  .todo {
    margin: 0;
    font-size: 0.9rem;
  }
  form {
    display: flex;
    flex-direction: column;
  }
  select {
    width: 14.5rem;
    font-size: 0.9rem;
  }
  .filter-todo {
    padding: 13px;
  }
  .select::after {
    padding: 13px;
  }
  form input {
    width: 12.5rem;
    font-size: 1.3rem;
  }
  form button {
    font-size: 1.3rem;
  }
}